import { useState, useEffect } from "react";
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";
import MKBox from "components/MKBox";
import MKTypography from "components/MKTypography";
import DefaultNavbar from "examples/Navbars/DefaultNavbar";
import SimpleFooter from "examples/Footers/SimpleFooter";
import 'style/css.css'
import routes from "routes";
import axios from "axios";
import Helper from "helper";

function PicPage() {
  const [eventList, setEventList] = useState([]);
  const [selectedEvent, setSelectedEvent] = useState({});

  useEffect(async () => {
    getListData();
  }, [])

  async function getListData() {
    try {
      const url = `${Helper.host}/restAPI/eventController.php?action=getEventReviewList`;
      const res = await axios.get(url, Helper.hostHeaders);
      if (res.data.result) {
        setEventList(res.data.result);
        setSelectedEvent(res.data.result[0]);
      } else {
        throw new Error(res.data.message);
      }
    } catch (e) {

    }
  }

  return (
    <>
      <DefaultNavbar
        routes={routes}
        action={false}
        sticky
      />
      <MKBox component="section" pt={20} pb={12}>
        <Container>
          <Grid container justifyContent="center">
            <Grid item xs={9}>
              <Card>
                <MKBox
                  variant="gradient"
                  bgColor="info"
                  borderRadius="lg"
                  coloredShadow="info"
                  p={3}
                  mt={-3}
                  mx={2}
                >
                  <MKTypography variant="h3" color="white">
                    {selectedEvent.event_title}
                  </MKTypography>
                  <MKTypography variant="body2" color="white" opacity={0.8}>
                    活动回顾
                  </MKTypography>
                </MKBox>
                <MKBox p={{ xs: 3, md: 6 }}>
                  <div dangerouslySetInnerHTML={{ __html: selectedEvent.event_review_content }}></div>
                </MKBox>
              </Card>
            </Grid>
            <Grid item xs={3}>
              {eventList.map(x => {
                return (
                  <MKBox ml={3} mb={1}>
                    <Card style={{ border: "1px solid #eee", cursor: "pointer" }} onClick={() => setSelectedEvent(x)}>
                      <MKTypography display="inline" variant="body2" px={3} py={1}>
                        {x.event_title}
                      </MKTypography>
                    </Card>
                  </MKBox>
                )
              })}
            </Grid>
          </Grid>
        </Container>
      </MKBox>
      <MKBox pt={{ xs: 0, lg: 3 }} pb={3}>
        <SimpleFooter />
      </MKBox>
    </>
  );
}

export default PicPage;
